"use client";
import React, { useState } from "react";
import Navbar from "./Navbar";
import Sidebar from "./Sidebar";
import Category from "./Category";
import Order from "./Order";
import "./Restaurant.css";

interface Ingredient {
  name: string;
  price: number;
  default: boolean;
}

interface Product {
  id: number;
  name: string;
  description: string;
  basePrice: number;
  image: string;
  calories: number;
  preparationTime: string;
  ingredients: Ingredient[];
}

const products: Product[] = [
  {
    id: 1,
    name: "Burger Deluxe",
    description: "Juicy beef burger with fresh toppings.",
    basePrice: 8.99,
    image: "/images/burger.jpg",
    calories: 650,
    preparationTime: "15 minutes",
    ingredients: [
      { name: "Lettuce", price: 0, default: true },
      { name: "Tomato", price: 0, default: true },
      { name: "Cheese", price: 1.5, default: false },
      { name: "Bacon", price: 2, default: false },
    ],
  },
];

const MenuCard: React.FC<{ name: string; description: string; price: number; imageUrl: string }> = ({
  name,
  description,
  price,
  imageUrl,
}) => (
  <div className="menu-card">
    <img src={imageUrl} alt={name} className="menu-card-image" />
    <div className="menu-card-content">
      <h3 className="menu-card-title">{name}</h3>
      <p className="menu-card-description">{description}</p>
      <p className="menu-card-price">${price.toFixed(2)}</p>
    </div>
  </div>
);

const Restaurant: React.FC = () => {
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);
  const [selectedIngredients, setSelectedIngredients] = useState<Set<string>>(new Set());

  return (
    <div className="restaurant-container">
      <Navbar />
      <div className="restaurant-main-content">
        <Sidebar />
        <div className="restaurant-menu">
          <Category />
          <div className="restaurant-menu-grid">
            {products.map((product) => (
              <MenuCard
                key={product.id}
                name={product.name}
                description={product.description}
                price={product.basePrice}
                imageUrl={product.image}
              />
            ))}
          </div>
        </div>
        <div className="restaurant-order-container">
          <Order />
        </div>
      </div>
    </div>
  );
};

export default Restaurant;
